import { Component, ErrorInfo, ReactNode } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { AlertTriangle, RefreshCw, Bug } from 'lucide-react';
import { logger } from '../utils/logger';
import LogViewer from './LogViewer';

interface Props {
  children: ReactNode;
  fallback?: ReactNode;
  componentName?: string;
}

interface State {
  hasError: boolean;
  error: Error | null;
  errorInfo: ErrorInfo | null;
}

class ErrorBoundary extends Component<Props, State> {
  public state: State = {
    hasError: false,
    error: null,
    errorInfo: null,
  };

  public static getDerivedStateFromError(error: Error): State {
    return {
      hasError: true,
      error,
      errorInfo: null,
    };
  }

  public componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    const componentName = this.props.componentName || 'Unknown';
    
    // 
    logger.error(`Error boundary caught error in ${componentName}`, {
      message: error.message,
      stack: error.stack,
      componentStack: errorInfo.componentStack,
      errorBoundary: componentName,
      props: this.props,
      timestamp: new Date().toISOString()
    }, 'error');

    this.setState({
      error,
      errorInfo,
    });

    console.error('ErrorBoundary caught an error:', error, errorInfo);
  }

  private handleReload = () => {
    logger.userAction('Error boundary reload clicked', {
      component: this.props.componentName || 'Unknown',
      error: this.state.error?.message
    });
    
    window.location.reload();
  };

  private handleReset = () => {
    logger.userAction('Error boundary reset clicked', {
      component: this.props.componentName || 'Unknown',
      error: this.state.error?.message
    });

    this.setState({
      hasError: false,
      error: null,
      errorInfo: null,
    });
  };

  public render() {
    if (this.state.hasError) {
      // 
      if (this.props.fallback) {
        return this.props.fallback;
      }

      // UI
      return (
        <div className="min-h-[400px] flex items-center justify-center p-4">
          <Card className="w-full max-w-lg">
            <CardHeader className="text-center">
              <div className="flex justify-center mb-4">
                <AlertTriangle className="h-12 w-12 text-red-500" />
              </div>
              <CardTitle className="text-red-600">
                
              </CardTitle>
              <CardDescription>
                {this.props.componentName ? 
                  `${this.props.componentName}` :
                  ''
                }
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              {/*  */}
              {this.state.error && (
                <div className="p-3 bg-red-50 border border-red-200 rounded-md">
                  <p className="text-sm text-red-800 font-medium">
                    :
                  </p>
                  <p className="text-sm text-red-700 mt-1">
                    {this.state.error.message}
                  </p>
                </div>
              )}

              {/*  */}
              {process.env.NODE_ENV === 'development' && this.state.error && (
                <details className="text-sm">
                  <summary className="cursor-pointer text-gray-600 mb-2">
                    
                  </summary>
                  <pre className="bg-gray-100 p-3 rounded text-xs overflow-x-auto">
                    {this.state.error.stack}
                  </pre>
                  {this.state.errorInfo && (
                    <pre className="bg-gray-100 p-3 rounded text-xs overflow-x-auto mt-2">
                      {this.state.errorInfo.componentStack}
                    </pre>
                  )}
                </details>
              )}

              {/*  */}
              <div className="flex flex-col sm:flex-row gap-3">
                <Button 
                  onClick={this.handleReset}
                  variant="default"
                  className="flex-1"
                >
                  <RefreshCw className="w-4 h-4 mr-2" />
                  
                </Button>
                
                <Button 
                  onClick={this.handleReload}
                  variant="outline"
                  className="flex-1"
                >
                  <RefreshCw className="w-4 h-4 mr-2" />
                  
                </Button>

                <LogViewer>
                  <Button variant="outline" size="sm">
                    <Bug className="w-4 h-4 mr-2" />
                    
                  </Button>
                </LogViewer>
              </div>

              {/*  */}
              <div className="text-sm text-gray-600 text-center pt-2">
                <p>
                  
                  
                </p>
              </div>
            </CardContent>
          </Card>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;
