import React, { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Switch } from '@/components/ui/switch';
import {
  Plus,
  Play,
  Pause,
  Square,
  RotateCcw,
  Trash2,
  Copy,
  Settings,
  Timer,
  Clock,
  CheckCircle,
  Circle,
  PlayCircle,
  PauseCircle,
} from 'lucide-react';
import { useMultiTimerStore } from '@/features/timer/stores/multi-timer-store';

const formatTime = (seconds: number): string => {
  const hours = Math.floor(seconds / 3600);
  const mins = Math.floor((seconds % 3600) / 60);
  const secs = seconds % 60;
  
  if (hours > 0) {
    return `${hours}:${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  }
  return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
};

const parseDuration = (input: string): number => {
  // MM:SS  H:MM:SS 
  const parts = input.split(':').map(Number);
  if (parts.length === 2) {
    return parts[0] * 60 + parts[1]; // MM:SS
  } else if (parts.length === 3) {
    return parts[0] * 3600 + parts[1] * 60 + parts[2]; // H:MM:SS
  }
  // 
  const minutes = parseInt(input, 10);
  return isNaN(minutes) ? 0 : minutes * 60;
};

interface TimerFormData {
  name: string;
  duration: string;
  category: string;
  description: string;
  color: string;
}

const TIMER_COLORS = [
  'bg-blue-500',
  'bg-green-500',
  'bg-purple-500',
  'bg-orange-500',
  'bg-red-500',
  'bg-pink-500',
  'bg-cyan-500',
  'bg-yellow-500',
  'bg-indigo-500',
  'bg-teal-500',
];

const AddTimerDialog: React.FC = () => {
  const { addTimer, categories, addCategory } = useMultiTimerStore();
  const [isOpen, setIsOpen] = useState(false);
  const [formData, setFormData] = useState<TimerFormData>({
    name: '',
    duration: '',
    category: '',
    description: '',
    color: TIMER_COLORS[0],
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.name || !formData.duration) return;
    
    const durationSeconds = parseDuration(formData.duration);
    if (durationSeconds <= 0) return;

    // 
    if (formData.category && !categories.includes(formData.category)) {
      addCategory(formData.category);
    }

    addTimer({
      name: formData.name,
      duration: durationSeconds,
      category: formData.category || undefined,
      description: formData.description || undefined,
      color: formData.color,
    });

    // 
    setFormData({
      name: '',
      duration: '',
      category: '',
      description: '',
      color: TIMER_COLORS[0],
    });
    setIsOpen(false);
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button>
          <Plus className="w-4 h-4 mr-2" />
          
        </Button>
      </DialogTrigger>
      
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle></DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <Label htmlFor="timer-name"></Label>
            <Input
              id="timer-name"
              placeholder=": "
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              required
            />
          </div>
          
          <div>
            <Label htmlFor="timer-duration">
              MM:SS  
            </Label>
            <Input
              id="timer-duration"
              placeholder=": 25:00, 10:30, 45"
              value={formData.duration}
              onChange={(e) => setFormData({ ...formData, duration: e.target.value })}
              required
            />
          </div>
          
          <div>
            <Label htmlFor="timer-category"></Label>
            <Select
              value={formData.category}
              onValueChange={(value: string) => setFormData({ ...formData, category: value })}
            >
              <SelectTrigger>
                <SelectValue placeholder="" />
              </SelectTrigger>
              <SelectContent>
                {categories.map((category) => (
                  <SelectItem key={category} value={category}>
                    {category}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          
          <div>
            <Label htmlFor="timer-description"></Label>
            <Textarea
              id="timer-description"
              placeholder=""
              value={formData.description}
              onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) => setFormData({ ...formData, description: e.target.value })}
              rows={2}
            />
          </div>
          
          <div>
            <Label></Label>
            <div className="flex gap-2 mt-2">
              {TIMER_COLORS.map((color) => (
                <button
                  key={color}
                  type="button"
                  className={`w-8 h-8 rounded-full ${color} ${
                    formData.color === color ? 'ring-2 ring-offset-2 ring-gray-400' : ''
                  }`}
                  onClick={() => setFormData({ ...formData, color })}
                />
              ))}
            </div>
          </div>
          
          <div className="flex justify-end gap-2 pt-4">
            <Button type="button" variant="outline" onClick={() => setIsOpen(false)}>
              
            </Button>
            <Button type="submit"></Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
};

const TimerCard: React.FC<{ timer: import('@/types/multi-timer').MultiTimer }> = ({ timer }) => {
  const {
    startTimer,
    pauseTimer,
    stopTimer,
    resetTimer,
    deleteTimer,
    duplicateTimer,
  } = useMultiTimerStore();

  const progress = timer.duration > 0 
    ? ((timer.duration - timer.remainingTime) / timer.duration) * 100 
    : 0;

  const getStatusIcon = () => {
    if (timer.isCompleted) return <CheckCircle className="w-5 h-5 text-green-500" />;
    if (timer.isRunning) return <PlayCircle className="w-5 h-5 text-blue-500" />;
    if (timer.isPaused) return <PauseCircle className="w-5 h-5 text-orange-500" />;
    return <Circle className="w-5 h-5 text-gray-400" />;
  };

  const getStatusText = () => {
    if (timer.isCompleted) return '';
    if (timer.isRunning) return '';
    if (timer.isPaused) return '';
    return '';
  };

  return (
    <Card className={`${timer.isRunning ? 'ring-2 ring-blue-200' : ''} ${timer.isCompleted ? 'bg-green-50' : ''}`}>
      <CardHeader className="pb-3">
        <div className="flex items-start justify-between">
          <div className="flex-1 min-w-0">
            <CardTitle className="text-lg truncate">{timer.name}</CardTitle>
            <div className="flex items-center gap-2 mt-1">
              {getStatusIcon()}
              <span className="text-sm text-muted-foreground">{getStatusText()}</span>
              {timer.category && (
                <Badge variant="secondary" className="text-xs">
                  {timer.category}
                </Badge>
              )}
            </div>
          </div>
          <div className={`w-4 h-4 rounded-full ${timer.color}`} />
        </div>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/*  */}
        <div className="text-center">
          <div className="text-3xl font-mono font-bold">
            {formatTime(timer.remainingTime)}
          </div>
          <div className="text-sm text-muted-foreground">
            / {formatTime(timer.duration)}
          </div>
        </div>
        
        {/*  */}
        <Progress 
          value={progress} 
          className="h-2"
          indicatorClassName={timer.color}
        />
        
        {/*  */}
        {timer.description && (
          <p className="text-sm text-muted-foreground line-clamp-2">
            {timer.description}
          </p>
        )}
        
        {/*  */}
        <div className="flex justify-center gap-2">
          {!timer.isRunning && !timer.isCompleted ? (
            <Button
              size="sm"
              onClick={() => startTimer(timer.id)}
              disabled={timer.isCompleted}
            >
              <Play className="w-4 h-4 mr-1" />
              
            </Button>
          ) : timer.isRunning ? (
            <Button
              size="sm"
              variant="outline"
              onClick={() => pauseTimer(timer.id)}
            >
              <Pause className="w-4 h-4 mr-1" />
              
            </Button>
          ) : null}
          
          <Button
            size="sm"
            variant="outline"
            onClick={() => stopTimer(timer.id)}
            disabled={!timer.isRunning && !timer.isPaused}
          >
            <Square className="w-4 h-4 mr-1" />
            
          </Button>
          
          <Button
            size="sm"
            variant="outline"
            onClick={() => resetTimer(timer.id)}
          >
            <RotateCcw className="w-4 h-4 mr-1" />
            
          </Button>
        </div>
        
        {/*  */}
        <div className="flex justify-center gap-2 pt-2 border-t">
          <Button
            size="sm"
            variant="ghost"
            onClick={() => duplicateTimer(timer.id)}
          >
            <Copy className="w-4 h-4 mr-1" />
            
          </Button>
          <Button
            size="sm"
            variant="ghost"
            onClick={() => deleteTimer(timer.id)}
            className="text-red-600 hover:text-red-700"
          >
            <Trash2 className="w-4 h-4 mr-1" />
            
          </Button>
        </div>
      </CardContent>
    </Card>
  );
};

const GlobalControls: React.FC = () => {
  const {
    timers,
    isAnyRunning,
    startAllTimers,
    pauseAllTimers,
    stopAllTimers,
    resetAllTimers,
    globalSettings,
    updateGlobalSettings,
  } = useMultiTimerStore();

  const hasTimers = timers.length > 0;
  const hasIncompleteTimers = timers.some(t => !t.isCompleted);

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Settings className="h-5 w-5" />
          
        </CardTitle>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/*  */}
        <div className="flex gap-2 flex-wrap">
          <Button
            onClick={startAllTimers}
            disabled={!hasIncompleteTimers}
            size="sm"
          >
            <PlayCircle className="w-4 h-4 mr-1" />
            
          </Button>
          
          <Button
            onClick={pauseAllTimers}
            variant="outline"
            disabled={!isAnyRunning}
            size="sm"
          >
            <PauseCircle className="w-4 h-4 mr-1" />
            
          </Button>
          
          <Button
            onClick={stopAllTimers}
            variant="outline"
            disabled={!hasTimers}
            size="sm"
          >
            <Square className="w-4 h-4 mr-1" />
            
          </Button>
          
          <Button
            onClick={resetAllTimers}
            variant="outline"
            disabled={!hasTimers}
            size="sm"
          >
            <RotateCcw className="w-4 h-4 mr-1" />
            
          </Button>
        </div>
        
        {/*  */}
        <div className="space-y-3 pt-4 border-t">
          <div className="flex items-center justify-between">
            <Label className="text-sm"></Label>
            <Switch
              checked={globalSettings.showNotifications}
              onCheckedChange={(checked) =>
                updateGlobalSettings({ showNotifications: checked })
              }
            />
          </div>
          
          <div className="flex items-center justify-between">
            <Label className="text-sm"></Label>
            <Switch
              checked={globalSettings.soundEnabled}
              onCheckedChange={(checked) =>
                updateGlobalSettings({ soundEnabled: checked })
              }
            />
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export const MultiTimerView: React.FC = () => {
  const { timers, isAnyRunning, tick, getRunningTimers, getCompletedTimers } = useMultiTimerStore();

  // tick
  useEffect(() => {
    if (isAnyRunning) {
      const interval = setInterval(tick, 1000);
      return () => clearInterval(interval);
    }
  }, [isAnyRunning, tick]);

  // 
  useEffect(() => {
    if ('Notification' in window && Notification.permission === 'default') {
      Notification.requestPermission();
    }
  }, []);

  const runningTimers = getRunningTimers();
  const completedTimers = getCompletedTimers();
  const waitingTimers = timers.filter(t => !t.isRunning && !t.isCompleted);

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/*  */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold flex items-center gap-2">
            <Timer className="h-6 w-6" />
            
          </h2>
          <p className="text-muted-foreground">
            
          </p>
        </div>
        <AddTimerDialog />
      </div>
      
      {/*  */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card className="text-center p-4">
          <div className="text-2xl font-bold text-blue-600">{runningTimers.length}</div>
          <div className="text-sm text-muted-foreground"></div>
        </Card>
        <Card className="text-center p-4">
          <div className="text-2xl font-bold text-orange-600">{waitingTimers.length}</div>
          <div className="text-sm text-muted-foreground"></div>
        </Card>
        <Card className="text-center p-4">
          <div className="text-2xl font-bold text-green-600">{completedTimers.length}</div>
          <div className="text-sm text-muted-foreground"></div>
        </Card>
        <Card className="text-center p-4">
          <div className="text-2xl font-bold text-purple-600">{timers.length}</div>
          <div className="text-sm text-muted-foreground"></div>
        </Card>
      </div>
      
      {/*  */}
      <GlobalControls />
      
      {/*  */}
      {timers.length === 0 ? (
        <Card className="text-center py-12">
          <CardContent>
            <Clock className="w-12 h-12 text-muted-foreground mx-auto mb-4" />
            <h3 className="text-lg font-semibold mb-2"></h3>
            <p className="text-muted-foreground mb-4">
              
            </p>
            <AddTimerDialog />
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {timers.map((timer) => (
            <TimerCard key={timer.id} timer={timer} />
          ))}
        </div>
      )}
    </div>
  );
};
